<template>
  <div v-if="datas">
		<div class="sort" v-for="(img, index) in imgs" :key="index" @click="goRankingBang(datas[index].title, datas[index]._id, datas[index].monthRank, datas[index].totalRank)">
			<img :src="publicPath+'img/'+img">
			<span>{{datas[index].title}}</span>
		</div>
		<div class="sort">
			<img src="../../public/img/seniority06.png">
			<span ref="texts">别人家的排行榜</span>
			<img ref="sortImg" class="arrows" :src="publicPath+'img/'+img" @click="negationContextsShow" >
		</div>
		<div class="hide-sort" v-for="text in condatas" :key="text._id" v-show="contextsShow" @click="goOthers(text._id)">
			<span>{{text.title}}</span>
		</div>
	</div>
</template>

<script>
export default {
	name: "Seni",
	props: ["sex"],
	data() {
		return {
			publicPath: process.env.BASE_URL,
			imgs: ['seniority01.png', 'seniority01.png', 'seniority01.png','seniority02.png','seniority03.png','seniority04.png','seniority05.png'],
			//男
			texts: [],
			contexts: [],
			datas: "",
			condatas: "",
			img: 'arrows-bottom.png',
			contextsShow: false,
		}
	},
	watch: {
		contextsShow: function(val) {
			if(val) {
				this.$refs.texts.style.color = 'red'
				this.img = 'arrows-top.png'	
			} else {
				this.$refs.texts.style.color = '#333'
				this.img = 'arrows-bottom.png'	
			}
		}
	},
	methods: {
		negationContextsShow() {
			this.contextsShow = !this.contextsShow
		},
		goRankingBang(name, id, monthrank, totalrank) {
			//id:周榜  monthRank: 月榜 totalRank: 总榜
			this.$router.push({path: '/RankingBang', query: {name: name, id: id, monthrank: monthrank, totalrank: totalrank}})
			console.log('榜的名称',name)
			console.log('周榜==>',id)
			console.log('月榜==>',monthrank)
			console.log('总榜==>',totalrank)
		},
		goOthers(text) {
			console.log(text)
			this.$router.push({path: '/Others', query: {id: text}})
		}
	},
	created() {
		
		this.axios.get("/api/ranking/gender").then(
			response => {
				if(this.sex) {
					//男
					console.log(response.data.male.slice(0, 7))
						this.datas = response.data.male.slice(0, 7) 
						this.condatas = response.data.male.slice(7)
				} else {
					//女
					this.datas = response.data.female.slice(0, 7) 
					this.condatas = response.data.female.slice(7)
				}
			}
		)
	}
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import '../../public/sass/mixin';

.sort {
	position: relative;
	display: flex;
	width: 100%;
	height: 60px;
	line-height: 60px;

	img {
		flex: 0 0 30px;
		margin: 15px 0 0 15px;
		width: 30px;
		height: 30px;
	}

	span {
		flex: 1;
		display: inline-block;
		width: calc(100%-15px);
		margin-left: 10px;
		vertical-align: top;
		@include border-bottom(rgb(241, 235, 235));
		font-size: 14px;
		color:#333;
	}

	img.arrows  {
		position: absolute;
		right: 10px;
		top: 50%;
		margin: 0;
		transform: translateY(-50%);
		width: 13px;
		height: 13px;
	}
}

.hide-sort {
	position: relative;
	display: flex;
	width: 100%;
	height: 60px;
	line-height: 60px;

	span {
		flex: 1;
		display: inline-block;
		width: calc(100%-15px);
		margin-left: 55px;
		vertical-align: top;
		@include border-bottom(rgb(241, 235, 235));
		font-size: 14px;
		color:#333;
	}
}
</style>
